import { signOut } from "@/app/api/auth/auth";

export const SignOut = (session: any) => {
  return (
    <>
      <form
        action={async () => {
          "use server";
          await signOut();
        }}
      >
        <button type="submit">Sign out with Facebook</button>
      </form>
      <img src={session.user.image} alt="User Avatar" />
      <button>redirect</button>
    </>
  );
};
